<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-select v-model="company_id" filterable clearable placeholder="请选择管理公司">
						<el-option v-for="item in company_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-select v-model="fund_type" clearable placeholder="基金类别" @change="chooseFundType">
						<el-option v-for="item in fund_type_list" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="fund_id" filterable clearable placeholder="请选择合伙企业">
						<el-option v-for="item in fund_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				    <!-- 在管基金总数<span>(&nbsp;{{ total }}&nbsp;)</span> -->
					管理公司在管基金总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn" @click="downloadExcel">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="company" label="管理公司全称" width="230">
				</el-table-column>
				<el-table-column sortable prop="fund_id" label="ID" width="80">
				</el-table-column>
				
				<el-table-column sortable prop="number" label="编号" width="80">
				</el-table-column>
				<el-table-column sortable prop="short_name" label="基金简称" width="250">
				</el-table-column>
				<el-table-column sortable prop="name" label="合伙企业" width="250">
				</el-table-column>
				<el-table-column sortable prop="fund_type_name" label="在管基金类别" width="150">
				</el-table-column>
				<el-table-column sortable prop="unit" label="币种" width="100">
					<template slot-scope="scope">
						{{ scope.row.unit == 1?'人民币':'美元' }}
					</template>
				</el-table-column> 
				<el-table-column sortable prop="multiple" label="倍数" width="100">
				</el-table-column>
				<el-table-column sortable prop="fair_value" label="最新公允价值（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="subscription_amount" label="认缴规模（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="amount_paid_in" label="实缴规模（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="income" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="irr" label="XIRR" width="120">
				</el-table-column>
				<el-table-column sortable prop="fund_end_date" label="基金期限（年）" width="150">
				</el-table-column>
				<el-table-column sortable prop="protocol_end_date" label="基金到期日" width="130">
				</el-table-column>
				<el-table-column sortable prop="fund_state_name" label="备案状态" width="130">
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getInvestorInvestGroupFundSelectListApi,fundListReportCompanyApi,fundDownloadReportCompanyApi } from '../../config/api'
	
	export default {
		name: 'manageCompanyFundTotal',
		data() {
			return {
				fund_type: '',
				company_id: '',
				fund_id: '',
				fund_type_list: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '自管基金'
					}
				],
				company_list: [],
				fund_list: [],
				currentPage: 1,
				pageSize: 10,
				total: 0,
				tableData: [ ],
			}
		},
		created() {
			this.getFundSelectList(1)
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				fundListReportCompanyApi({
					company_id: this.company_id,
					fund_type: this.fund_type,
					fund_id: this.fund_id,
					page: this.currentPage,
					pageSize: this.pageSize
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getFundSelectList(e){
				getInvestorInvestGroupFundSelectListApi({
					type: e
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						if (e == 1) {
							this.company_list = res.data.payload;
						} else{
							this.fund_list = res.data.payload;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				fundDownloadReportCompanyApi({
					company_id: this.company_id,
					fund_type: this.fund_type,
					fund_id: this.fund_id,
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseFundType(e){
				this.fund_id = '';
				if (e) {
					this.getFundSelectList(e+1)
				} else{
					this.fund_list = [];
				}
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
</style>


